html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    background-image: url("../img/bg-pattern-desktop.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: hsl(0, 93%, 98.5%);
  }
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  margin: 0;
  line-height: 1;
  letter-spacing: 1.3rem;
  font-weight: 400;
  font-size: 4rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  h1 {
    font-size: 5.4rem;
  }
}

p {
  line-height: 1.5;
}

.logo {
  position: relative;
}
.logo img {
  width: auto;
  position: absolute;
  top: -8rem;
}
@media (min-width: 768px) {
  .logo img {
    top: 5rem;
  }
}

.base__flex {
  margin: 13rem 0 0 0;
  display: flex;
  flex-direction: column-reverse;
  background-color: hsl(0, 93%, 98%);
}
@media (min-width: 768px) {
  .base__flex {
    background-color: unset;
    margin: 0;
    flex-direction: row;
    align-items: center;
    gap: 4rem;
  }
}
.base__contenido {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .base__contenido {
    max-width: 55rem;
    padding: 5rem 0 5rem 2rem;
  }
}
@media (min-width: 1024px) {
  .base__contenido {
    padding: 0;
  }
}
.base__heading {
  color: hsl(0, 6%, 24%);
  text-align: center;
}
.base__heading span {
  display: block;
  font-weight: 300;
  color: hsl(0, 36%, 70%);
}
@media (min-width: 768px) {
  .base__heading {
    text-align: left;
  }
}
.base__texto {
  text-align: center;
  color: hsl(0, 36%, 70%);
}
@media (min-width: 768px) {
  .base__texto {
    text-align: left;
  }
}

.base__imagen {
  background-size: cover;
  background-repeat: no-repeat;
  max-width: auto;
  height: 25rem;
}
@media (min-width: 768px) {
  .base__imagen {
    width: 61rem;
    height: 100vh;
    background-position: center;
  }
}

.notavif.notwebp .base__imagen {
  background-image: url(../img/hero-mobile.jpg);
}
@media (min-width: 768px) {
  .notavif.notwebp .base__imagen {
    background-image: url(../img/hero-desktop.jpg);
  }
}

.webp .base__imagen {
  background-image: url("../img/hero-mobile.webp");
}
@media (min-width: 768px) {
  .webp .base__imagen {
    background-image: url("../img/hero-desktop.webp");
  }
}

.avif .base__imagen {
  background-image: url("../img/hero-mobile.avif");
}
@media (min-width: 768px) {
  .avif .base__imagen {
    background-image: url("../img/hero-desktop.avif");
  }
}

.formulario {
  display: flex;
  align-items: center;
  border-radius: 3rem;
  border: 0.1rem solid hsl(0, 36%, 70%);
  position: relative;
}
.formulario__input {
  padding: 1rem;
  width: 80%;
  border: none;
  background-color: transparent;
  outline: none;
  color: hsl(0, 36%, 70%);
}
@media (min-width: 768px) {
  .formulario__input {
    width: 85%;
  }
}
.formulario__input-error {
  background-image: url("../img/icon-error.svg");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: right;
}
.formulario__btn {
  background: linear-gradient(to right, hsl(0, 80%, 86%), hsl(0, 74%, 74%));
  border: none;
  border-radius: 3rem;
  box-shadow: 0px 5px 5px 5px hsla(0, 93%, 94%, 0.8);
  padding: 1.1rem 2.2rem;
  cursor: pointer;
  position: absolute;
  right: -0.1rem;
  transition: background 0.3s ease;
}
.formulario__btn:hover {
  background: linear-gradient(to right, hsl(0, 80%, 91%), hsl(0, 74%, 79%));
  box-shadow: 0px 5px 5px 3px hsla(0, 36%, 80%, 0.8);
}
@media (min-width: 768px) {
  .formulario__btn {
    padding: 1.1rem 3rem;
  }
}
.formulario__mensaje {
  margin: 0.5rem 0 0 2rem;
}
.formulario__mensaje-correcto {
  color: hsl(145, 78%, 43%);
}
.formulario__mensaje-error {
  color: hsl(0, 93%, 68%);
}

.footer {
  position: relative;
  text-align: center;
}
.footer__attribution {
  position: absolute;
  bottom: 0;
  top: 2rem;
}
@media (min-width: 768px) {
  .footer__attribution {
    top: -5rem;
  }
}
.footer__attribution a {
  text-decoration: none;
  color: hsl(0, 36%, 70%);
  font-weight: 700;
}